<style lang="less" src="./ReturnDetail.less" scoped></style>

<template>
    <layout-base id="ReturnDetail">
        <v-header slot="header" class="header">
            <div class="title" slot="center"></div>
        </v-header>
        <div class="page" v-if="detail">
            <van-cell title="" :value="detail.CustomerName"></van-cell>
            <van-cell title="" :value="detail.TypeName"></van-cell>
            <div class="box">
                <div class="hd"></div>
                <div class="bd">
                    <p v-if="!detail.Content"></p>
                    <p v-else>{{detail.Content}}</p>
                </div>
            </div>
            <div class="box">
                <div class="hd"></div>
                <div class="bd">
                    <p v-if="!detail.ImgID"></p>
                    <img :src="v" v-for="(v, i) in imgs" :key="i" @click="showImg(i)">
                </div>
            </div>
        </div>
    </layout-base>
</template>

<script>
import { ConsultType } from '../../assets/js/const.js'
import { ImagePreview } from 'vant'

export default {
    data () {
        return {
            imgs: [],
            detail: {}
        }
    },
    methods: {
        showImg (_i) {
            ImagePreview({
                images: this.imgs,
                startPosition: _i
            })
        }
    },
    created () {
        this.$ajax({
            url: '/employee/ReturnVisit/getDetail',
            data: {
                ID: this.$route.query.ID
            }
        }).then((_res) => {
            this.detail = _res
            this.detail.TypeName = ConsultType[_res.Type].name
            let imgs = _res.ImgID.split('|')
            imgs.pop()
            this.imgs = imgs.map(_item => this.$imgHost + _item)
        })
    }
}
</script>