<style lang="less" src="./CareAdd.less" scoped></style>

<template>
    <layout-base id="CareAdd">
        <v-header class="header" slot="header">
            <div class="title" slot="center"></div>
        </v-header>
        <div class="page">
            <van-cell title="" is-link @click="showC = true">
                <span v-if="customer.Name">{{customer.Name}}</span>
                <span class="placeholder" v-else></span>
            </van-cell>
            <van-cell title="">
                <div class="input">
                    <input type="text" v-model="content" placeholder="">
                </div>
            </van-cell>
            <van-cell title="" is-link @click="showD = true">
                <span v-if="date">{{date}}</span>
                <span class="placeholder" v-else></span>
            </van-cell>
        </div>

        <div class="button" @click="submit"></div>

        <van-popup v-model="showD">
            <van-datetime-picker v-model="now" type="date" @confirm="confirm" @cancel="cancel" />
        </van-popup>
        
        <customer-search v-model="showC" @Select="selectCustomer"></customer-search>
    </layout-base>
</template>

<script>
import CustomerSearch from '../../components/CustomerSearch/CustomerSearch.vue'

export default {
    components: {
        CustomerSearch
    },
    data () {
        return {
            showC: false,
            showD: false,
            currentDate: +new Date(),
            now: new Date(),
            content: '',
            customer: {
                ID: this.$route.query.ID,
                Name: this.$route.query.Name
            }
        }
    },
    computed: {
        date () {
            return new Date(this.currentDate).DateFormat()
        }
    },
    methods: {
        cancel () {
            this.showD = false
        },
        confirm (_v) {
            this.showD = false
            this.currentDate = +new Date(_v)
        },
        selectCustomer (_c) {
            this.customer = _c
        },
        submit () {
            if (!this.content) {
                this.$toast('')
                return;
            }
            this.$dialog.confirm({
                title: ''
            }).then(() => {
                this.$ajax({
                    url: '/customer/Care/addNewCarePlan',
                    data: {
                        CustomerID: this.customer.ID,
                        CareTypeName: this.content,
                        SDate: this.date
                    }
                }).then(() => {
                    this.$dialog.confirm({
                        title: '',
                        message: ''
                    }).then(() => {
                        this.customer = {}
                        this.content = ''
                        this.currentDate = +new Date()
                    }).catch(() => {
                        this.$router.go(-1)
                    })
                })
            }).catch(() => {})
        }
    }
}
</script>