<style lang="less" src="./CureRegister.less" scoped></style>

<template>
    <layout-base id="CureRegister">
        <v-header class="header" slot="header">
            <div class="title" slot="center"></div>
        </v-header>
        <div class="page">
            <van-cell title="" :value="info.info.CustomerName"></van-cell>
            <van-cell title="" :value="info.info.ProjectName"></van-cell>
            <van-cell title="" :value="`${info.info.Times + 1}`" v-if="info.type == 0"></van-cell>
            <van-cell title="" value="" v-else></van-cell>
            <v-form-textarea v-model="content" title="" placeholder=""></v-form-textarea>
            <cure-uploader @RemoveImg="removeImg" @OnRead="onRead"></cure-uploader>

            <div class="button" @click.stop="submit()"></div>
        </div>
    </layout-base>
</template>

<script>
import CureUploader from '../../components/CureUploader/CureUploader.vue'

export default {
    components: {
        CureUploader
    },
    data () {
        return {
            info: this.$route.query,
            content: '',
            imgID: ['', '', '']
        }
    },
    methods: {
        removeImg (_i) {
            this.imgID[_i] = ''
        },
        onRead (_img, _i) {
            this.imgID[_i] = _img
        },
        submit () {
            if (!this.content) {
                this.$toast(`${['', ''][this.info.type]}`)
                return;
            }
            this.$dialog.confirm({
                title: '',
                message: ''
            }).then(() => {
                this.$ajax({
                    url: ['/feedback/TreatmentEffect/save', 'feedback/SideReaction/create'][this.info.type],
                    data: {
                        ID: this.info.info.ID,
                        CustomerID: this.info.info.CustomerID,
                        ProjectName: this.info.info.ProjectName,
                        ProjectID: this.info.info.ProjectID,
                        Content: this.content,
                        ImgID: this.imgID
                    }
                }).then(() => {
                    this.$toast('')
                })
            }).catch(() => {})
        }
    }
}
</script>