<style lang="less" src="./CareRegister.less" scoped></style>

<template>
    <layout-base id="CareRegister">
        <v-header class="header" slot="header">
            <div class="title" slot="center"></div>
        </v-header>
        <div class="page">
            <van-cell title="" :value="query.Name"></van-cell>
            <v-form-textarea v-model="content" title="" placeholder=""></v-form-textarea>
            <v-form-uploader title="" v-model="imgs" @OnRead="onRead" @RemoveImg="removeImg" placeholder=""></v-form-uploader>
        </div>
        <div class="button" @click.stop="submit()"></div>
    </layout-base>
</template>

<script>
export default {
    data () {
        return {
            query: this.$route.query,
            content: '',
            imgs: [],
            imgID: []
        }
    },
    methods: {
        onRead (_data) {
            if (this.imgID.length < 5) {
                this.imgs.push(this.$imgHost + _data)
                this.imgID.push(_data)
            } else {
                this.$toast('6')
            }
        },
        removeImg (_v) {
            this.imgID.splice(this.imgs.indexOf(_v), 1)
        },
        submit () {
            if (!this.content) {
                this.$toast('')
                return;
            }
            this.$dialog.confirm({
                title: '',
                message: ''
            }).then(() => {
                this.$ajax({
                    url: '/customer/Care/saveCareRecord',
                    data: {
                        ID: this.query.ID,
                        Content: this.content,
                        ImgID: this.imgID
                    }
                }).then(() => {
                    this.$toast('')
                    this.$router.go(-1)
                })
            }).catch(() => {})
        }
    }
}
</script>