<style lang="less" src="./FollowRecordDetail.less" scoped></style>

<template>
    <layout-base id="FollowRecordDetail">
        <v-header slot="header" class="header">
            <div class="title" slot="center"></div>
        </v-header>
        <div class="page" v-if="detail">
            <van-cell title="" :value="detail.CustomerName"></van-cell>
            <van-cell title="" :value="detail.TypeName"></van-cell>
            <van-cell title="" :value="detail.AskProject"></van-cell>
            <div class="box">
                <div class="hd"></div>
                <div class="bd">
                    <p v-if="!detail.AskContent"></p>
                    <p v-else>{{detail.AskContent}}</p>
                </div>
            </div>
            <div class="box">
                <div class="hd"></div>
                <div class="bd">
                    <p v-if="!detail.ImgUrl"></p>
                    <img :src="v" v-for="(v, i) in imgs" :key="i" @click="showImg(i)">
                </div>
            </div>
        </div>
    </layout-base>
</template>

<script>
import { ConsultType } from '../../assets/js/const.js'
import { ImagePreview } from 'vant'

export default {
    data () {
        return {
            imgs: [],
            detail: {}
        }
    },
    methods: {
        showImg (_i) {
            ImagePreview({
                images: this.imgs,
                startPosition: _i
            })
        }
    },
    created () {
        this.detail = this.$route.query.Info
        this.detail.TypeName = ConsultType[this.detail.AskSource].name
        let imgs = this.detail.ImgUrl.split('|')
        imgs.pop()
        this.imgs = imgs.map(_item => this.$imgHost + _item)
    }
}
</script>