<style lang="less" src="./ProposalAdd.less" scoped></style>

<template>
    <layout-base id="ProposalAdd">
        <v-header class="header" slot="header">
            <div class="title" slot="center"></div>
        </v-header>
        <div class="page">
            <van-cell title="" :value="Name"></van-cell>
            <v-form-textarea v-model="content" title="" placeholder=""></v-form-textarea>
            <div class="button" @click.stop="submit()"></div>
        </div>
    </layout-base>
</template>

<script>
export default {
    data () {
        return {
            Name: this.$route.query.Name,
            content: ''
        }
    },
    methods: {
        submit () {
            if (!this.content) {
                this.$toast(``)
                return;
            }
            this.$dialog.confirm({
                title: ''
            }).then(() => {
                this.$ajax({
                    url: '/feedback/Opinion/create',
                    data: {
                        CustomerID: this.$route.query.ID,
                        Content: this.content
                    }
                }).then(() => {
                    this.$dialog.confirm({
                        title: '',
                        message: ''
                    }).then(() => {
                        this.content = ''
                    }).catch(() => {
                        this.$router.go(-1)
                    })
                })
            }).catch(() => {})
        }
    }
}
</script>